header
    position: fixed
    width: 100%
    margin: 0 auto
    z-index: 999
    box-shadow: 1px 0 15px rgba(0, 0, 0, 0.5)
    background: #333e48
    .header_top
        .header_top-social
            ul
                
                display: flex
                justify-content: flex-end
                align-items: center
                margin-bottom: 0
                padding: 10px 0 0
                li
                    list-style: none
                    padding-left: 15px
                    transition: .5s all
                    i
                        color: #b7892e
                    &:first-child
                        i
                            font-size: 18px
                    &:last-child
                        i
                            font-size: 18px
                    &:hover
                        i
                            color: #888
    .header_bottom
        .navbar
            padding: 0 
            
            .navbar-nav
                justify-content: space-between
                align-items: center
                flex: 1
                padding-right: 20px
                li
                    padding: .5rem 0
                    .nav-link
                        padding: 0
                    a
                        color: rgba(255, 255, 255, .5)
                        font-weight: 600
                        text-decoration: none
                        transition: .5s all
                        white-space: nowrap
                        &.active
                            color: #fff
                    &:hover
                        a
                            color: #fff
                    
                            
                               
                            
            .form-inline
                background: #fff
                border-radius: 30px
                input
                    border: none
                    border-radius: 30px 0 0 30px
                    margin-right: 0 !important
                    width: 100px
                    padding: 0 0 0 15px
                    height: 20px
                    &:focus
                        box-shadow: none
                        outline: none
                button
                    border: none
                    border-radius: 0
                    color: #b7892e
                    transition: .5s all
                    padding: 0 5px 0 0
                    &:hover
                        background: none
                        color: #888
                    &:focus
                        box-shadow: none
                        outline: none
    @media (max-width: 992px)
        .header_bottom
            .navbar
                text-align: center
                .navbar-nav
                    padding-right: 0
                    li
                        margin-bottom: 15px
                .form-inline
                    display: inline-flex
                    justify-content: space-between
                    margin-bottom: 40px !important
                    input
                        width: auto
                        flex: 1
                    
                        
                
                    
                
                    