footer
    background: #333e48
    padding: 30px 0
    .footer_item
        height: 100%
        display: flex
        align-items: center
        // justify-content: center
    .footer_logo
        text-align: center
        img
            width: 100px
    .footer_item
        ul
            padding-left: 0
            li
                list-style: none
                color: #fff
                font-size: 14px
                margin-bottom: 10px
                img
                    padding-right: 10px
                a
                    text-decoration: none
    .footer_icon-social
        justify-content: center
        ul
            display: flex
            align-items: center
            
            li
                padding: 10px
                a
                    color: $main
                    transition: .5s all
                    i
                        font-size: 25px
                &:hover
                    a
                        color: #888
    @media (max-width: 768px)
        .footer_item
            ul
                li
                    margin-bottom: 15px



                    
                    