.VMN-services-sec1
    padding: 80px 0 30px
    background: #eaebec
    .services-sec1-introduction
        padding: 0 11% 50px 11%
        text-align: center
        h2
            color: #3d4544
            font-weight: 300 
            letter-spacing: 3px
            font-size: 30px
    .services-sec1-content
        .content_item
            &:first-child
                margin-bottom: 80px
            &:last-child
                .sec1_content-describe
                    padding-left: 0
                .sec1_content-img
                    text-align: right
                
    .sec1_content-img
        margin-bottom: 20px
        img
            width: 90%
            
    
    .sec1_content-describe
        padding-left: 10%
        h2
            font-size: 30px
            color: #6f8083
            font-weight: 400
            margin-bottom: 30px
            padding-left: 5px
            letter-spacing: 5px
        h4 
            line-height: 1
            color: #737f82
            font-size: 18px
            font-weight: 600
           
        span
            color: #737f82
            font-size: 18px
            font-weight: 600
        p
            color: #737f82
            font-size: 18px
            text-align: justify
            padding-bottom: 30px
            margin-top: 5px
            
// Responsive screen 992px
@media (max-width: 992px)
    .VMN-services-sec1
        .services-sec1-introduction
            h2
                font-size: 18px
// Section-2 Services
.VMN_services-sec2
    background: #c7cecd
    padding: 40px 0 30px
    .sec2_content
        position: relative
        .sec2_title
            h2
                font-size: 80px
                font-weight: 300
                color: #293433 
                letter-spacing: 8px
        .sec2_desc
            display: inline-block
            margin-right: 10%
            position: relative
            p
                margin-top: 10px
                font-size: 30px
                color: #3d4544
                letter-spacing: 5px
                // text-align: left
            .item_paper-small
                position: absolute
                bottom: -100%
                right: -40%
    @media (max-width: 768px)
        .sec2_content
            .sec2_title
                h2
                    font-size: 30px
            .sec2_desc
                p
                    font-size: 18px
                .item_paper-small
                    right: -60%
    @media (max-width: 576px)
        .sec2_content
            .sec2_desc
                margin-right: 0
                .item_paper-small
                    display: none
// Section-3 services
.VMN_services-sec3 
    background: url('../../assets/img/services/services-background-4.png')
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    .services_list
        width: 650px
        background: rgba(0, 0, 0, .7)
        padding: 50px 0 50px 100px
        .title_table
            h2
                color: #fff
                letter-spacing: 5px
                font-weight: 400
                padding-left: 10px
                font-size: 40px
                margin-bottom: 10px
        .table
            margin-bottom: 0
            padding-left: 100px
            tr
                th
                    text-transform: uppercase
                th,td
                    color: #fff
                    padding: 5px
    @media (max-width: 768px)
        .services_list
            width: auto
            padding: 50px 20px

// Section-4 services
.VMN_services-sec4 
    padding: 50px 0 20px
    position: relative
    .svc_sec4-title
        h3 
            color: #293433
            font-size: 30px
            letter-spacing: 5px
            font-weight: 400
    .col-item-1
        padding: 0 80px
    .col-item-2
        padding-left: 50px
    .sec4_items
        margin-bottom: 30px
        position: relative
        z-index: 9
        .item_info
            p
                color: $blue
            .title
                text-transform: uppercase
            .desc
                text-align: justify
            .item_price
                display: flex
                align-items: center
                .name
                    flex-grow: 1
                    flex-basis: 30%
                .price
                    flex-grow: 10
            .item_img
                img
                    width: 100%
        &:last-child
            margin-bottom: 0
    .item_img-wall
        position: absolute
        bottom: 0
        right: 5%
        z-index: 8
        img
            width: 300px
    @media (max-width: 992px)
        .col-item-1
            padding: 0 15px
        .col-item-2
            padding-left: 15px
        .item_img-wall
            display: none
        .sec4_items
            &:last-child
                margin-bottom: 30px
    @media (max-width: 768px)
        .sec4_items
            .item_info
                .item_price
                    .price
                        flex-grow: 0
                .item_img
                    img
                        width: 100% !important
            
// SECTION-5 services
.VMN_services-sec5 
    background: #a0adac
    padding: 50px 0 20px
    .svc_sec5-title
        h2
            font-weight: 300
            color: #293433
            letter-spacing: 5px
            font-size: 40px
    .sec5_items
        margin-bottom: 30px
        p
            color: #fff
            font-weight: 500
            line-height: 1.7
        .item_price
            display: flex
            .name
                flex-grow: 10
            .price
                flex-grow: 1

// Section-6 services
.VMN_services-sec6
    padding: 50px 0
    background: #efefef
    .sec6_title
        h2
            font-weight: 300
            font-size: 50px
            letter-spacing: 5px
            margin-bottom: 20px
        .title_desc
            margin-bottom: 30px
            p
                font-size: 18px
    .sec6_content 
        max-width: 992px
        margin: 0 auto
        .sec6_content-img
            img
                width: 100%
        .sec6-content_items
            .item_price
                display: flex
                .name
                    flex-grow: 1
                .price
                    flex-grow: 0
                p
                    color: $blue
                    font-size: 16px
                    font-weight: 500
                    margin-bottom: 10px
    @media (max-width: 992px)
        .sec6_content 
            .sec6_content-img
                margin-bottom: 30px
// SECTION-7 services
.VMN_services-sec7
    background: #e3e5e5
    padding: 50px 0
    .sec7_content-box 
        background-image: url('../../assets/img/services/services-background-2.png')
        background-size: cover
        background-position: center
        background-repeat: no-repeat
        padding: 30px 50px 
        .content_items
            padding: 20px 30px
            width: 500px
            border: 1px solid #fff
            border-radius: 15px
    
            .item_title
                h2 
                    color: #fff
                    font-weight: 300
                    letter-spacing: 5px
                    opacity: .8
            .item_price
                display: flex
                margin-bottom: 5px
                p
                    color: #fff
                .name
                    flex-grow: 1
                    flex-basis: 50%
                .price
                    flex-grow: 8
    @media (max-width: 768px)
        .sec7_content-box 
            padding: 10px
            .content_items
                padding: 15px
                width: auto
// Section-8 services
.VMN_services-sec8
    background-image: url('../../assets/img/services/services-background-3.png')
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    .sec8_content
        display: flex
        justify-content: flex-end
        align-items: center
        padding: 60px 20px 60px 0
        h2
            font-size: 46px
            color: #fff
            letter-spacing: 5px
            font-weight: 300
            opacity: .8
            margin-bottom: 10px
        p
            color: #fff
            font-size: 26px
            text-align: center
            opacity: .8
    @media (max-width: 992px)
        .sec8_content
            // justify-content: center
            h2
                font-size: 30px
            p
                font-size: 18px
    @media (max-width: 576px)
        .sec8_content
            padding: 15px
            justify-content: center
            h2
                text-align: center
// Section-9 Services
.VMN_services-sec9 
    padding: 50px 0
    .services_gallery
        .gallery_col-fisrt 
            max-width: 56%
            padding-right: 0
            .fisrt_small
                padding-right: 0
            .fisrt_small-1
                padding-left: 0
            .item_img-1
                height: 100%
                
        .gallery_col-last
            max-width: 44%
            padding-left: 0

        .gallery_item
            // height: 100%
            img
                width: 100%
                height: 100%
        .gallery_title
            h3
                color: #7c868e
                font-weight: 300
                font-size: 40px
                text-align: center
                margin-top: 5px
                letter-spacing: 2px
    .sec9_content 
        .items
            margin-bottom: 20px
            p
                color: $blue
                font-weight: 500
            .item_title
                text-transform: uppercase
            .item_price
                display: flex
                .name
                    flex-grow: 1
                .price
                    flex-grow: 0
            .item_fill
                display: flex
                padding-left: 15px
                margin-bottom: 5px
                .name
                    flex-basis: 25%
        .items_bottom 
            p
                color: $blue
                font-weight: 500
            .item_price 
                display: flex
                .name
                    flex-grow: 1
                    flex-basis: 40%
                .price
                    flex-grow: 2
    @media (max-width: 992px)
        .services_gallery
            margin-bottom: 30px
            .gallery_title
                h3
                    font-size: 30px
    @media (max-width: 576px)
        .services_gallery
            .gallery_title
                h3
                    margin-top: 15px
                    font-size: 20px
        .sec9_content
            .items
                .item_price 
                    .name
                        flex-basis: 70%
                    
                .item_fill
                    .name
                        flex-basis: 33.33%
                        flex-wrap: wrap
            .items_bottom 
                .item_price 
                    .name
                        flex-grow: 1
                    .price
                        flex-grow: 0


            
            
        