.Vnail_blog-title 
    padding-top: 50px
    .main-title
        margin-bottom: 30px
        h2 
            color: $blue
            font-weight: 400
            letter-spacing: 5px
            font-size: 40px
            text-transform: uppercase
.Vnail_blog-sec1
    background: #e6e8e8
    .container
        max-width: 1200px
    .sec1_content
        display: flex
        flex-direction: column
        justify-content: center
        height: 100%
        .sec1_content-title
            margin-bottom: 20px
            h3
                font-weight: 400
                font-size: 36px
                margin-left: 5px
                color: #738282
        .ff-segoe
            font-size: 14px
            font-weight: 600
        .content_detail
            
            .date
                display: flex
                align-items: center
                margin-left: 5px
                date
                    margin-left: 5px
                    color: $blue
            .brief-content
                
                p
                    overflow: hidden
                    text-overflow: ellipsis
                    line-height: 20px
                    -webkit-line-clamp: 5
                    height: 100px
                    display: -webkit-box
                    -webkit-box-orient: vertical
                    color: $blue
                    font-size: 16px
    .sec1_img
        img
            width: 100%
            position: relative
            top: -15px
    @media (max-width: 992px)
        .sec1_content
            padding: 20px 0
            align-items: center
            .sec1_content-title
                h3
                    font-size: 22px
            .text-border-bottom
                &::after
                    display: none
        .sec1_img
            img
                width: 100%
                position: relative
                top: 0
.Vnail-newsletter
    .newsletter-top
        background: #dee1e0
        padding: 30px 10px 10px
        .newsletter-big
            padding: 0 20px
            .item
                margin-bottom: 20px
                a
                    text-decoration: none
                .image
                    height: 272px
                    position: relative
                    overflow: hidden
                    img 
                        height: 272px
                        position: absolute
                        width: 100%
                        object-fit: contain
                figcaption
                    padding: 10px
                    
                .title
                    h3 
                        font-size: 18px
                        font-weight: 600
                        color: #5b6670
                .brief-content
                    p
                        overflow: hidden
                        text-overflow: ellipsis
                        line-height: 25px
                        -webkit-line-clamp: 4
                        height: 100px
                        display: -webkit-box
                        -webkit-box-orient: vertical
                .date
                    font-size: 14px
                    color: #241111
                    margin-bottom: 5px
        .newsletter-small
            .item
                margin-bottom: 20px
                a
                    text-decoration: none
                .image
                    height: 200px
                    position: relative
                    overflow: hidden
                    img 
                        height: 200px
                        position: absolute
                        width: 100%
                        object-fit: cover
                figcaption
                    padding: 10px
                    
                .title
                    h3 
                        font-size: 16px
                        font-weight: 600
                        color: #5b6670
                        line-height: 1.2
                .brief-content
                    p
                        overflow: hidden
                        text-overflow: ellipsis
                        line-height: 25px
                        -webkit-line-clamp: 4
                        height: 100px
                        display: -webkit-box
                        -webkit-box-orient: vertical
                .date
                    font-size: 14px
                    color: #666666
                    margin-bottom: 5px
