//- About Section 7
.VMN_about-sec7
    padding: 50px 0
    .about_swiper
        position: relative
    .swiper-slide
        height: auto
        padding-top: 0
    .sec7_item
        display: block
        position: relative
        transition: .5s all
        height: 100%
        overflow: hidden
        &::after
            content: ''
            width: 100%
            height: 100%
            background: #fff
            opacity: .5
            top: 0
            left: 0
            position: absolute
            transition: .5s all
        &:hover
            &::after
                opacity: 0
            img
                transform: scale(1.2)
                
        img
            width: 100%
            height: 100%
            transition: .5s all
            // object-fit: cover
            position: relative
    .swiper-pagination
        width: 100%
        margin: 0 auto
        padding-top: 10px
        .swiper-pagination-bullet
            margin-right: 5px
            width: 12px
            height: 12px
            background-color: #8f9d9d
            
