.VMN_gallery-sec1
    padding: 50px 0
    background: url('../../assets/img/gallery/gallery-background-1.png')
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    .gallery_title
        h3
            font-size: 30px
            color: #000 
            font-weight: 400 
            letter-spacing: 2px
        .sec1_title
            h2
                font-size: 70px
                font-weight: 600
                color: #6f808f
                span
                    font-weight: 300
                    margin-left: 20px
        p
            color: #6f808f
            // font-weight: 600
        .text-border-bottom
            &::after
                left: 5%
                bottom: -10px
    .swiper_items
        padding: 50px 0 0
        .swiper-container
            padding: 15px
        .swiper-slide
            height: auto
            padding-top: 0
            .item_img
                position: relative
                display: inline-block
                img
                    position: relative
                    z-index: 9
                &::after
                    content: ''
                    width: calc( 100% + 20px  )
                    height: calc( 100% + 20px  )
                    border: 1px solid #fff
                    border-radius: 50%
                    top: -10px
                    left: -10px
                    position: absolute
                .img_decorate-1
                    position: absolute
                    top: 40%
                    left: -39%
                    transform: translateY(-50%)
                    img
                        width: 100%
                .img_decorate-2
                    position: absolute
                    top: 0
                    right: -15%
                    z-index: 10
                    img
                        width: 100%
                .img_decorate-3
                    position: absolute
                    bottom: -5%
                    left: -15%
                    z-index: 10
                    img
                        width: 100%
        .swiper-pagination
            display: flex
            align-items: center
            position: relative
            justify-content: center
            .swiper-pagination-bullet
                width: 13px
                height: 13px
                background: #8f9d9d
                margin: 20px 5px 0
@media (max-width: 992px)
    .VMN_gallery-sec1
        padding: 50px 0 
@media (max-width: 768px)
    .VMN_gallery-sec1
        .gallery_title
            h3
                font-size: 22px
                letter-spacing: 1px
            .sec1_title
                h2
                    font-size: 30px
                    span
                        margin-left: 10px
        .swiper_items
            .swiper-slide
                .item_img
                    .img_decorate-2
                        width: 100px
                    .img_decorate-3
                        display: none

@media (max-width: 576px)
    .VMN_gallery-sec1
        padding: 50px 0

// Section-2 Gallery
.VMN_gallery-sec2
    padding: 50px 0 0
    .sec2_items
        margin-bottom: 30px
        img
            width: 100%