.VMN_home-sec1 
    padding: 50px 0
    background: #efefef
    .home_sec1-box 
        position: relative
        .animation_box-after
            position: absolute
            bottom: 0
            right: 0
            img
                width: 100%
        .animation_box-before
            position: absolute
            bottom: -20%
            left: 0
            z-index: 9
            img
                width: 100%
            &::after
                content: ''
                width: calc( 100% + 50px )
                height: 80px
                position: absolute
                background: rgba(110,125,125, .3)
                left: 0
                bottom: 15%
            &::before
                content: ''
                width: 40%
                height: 80px
                position: absolute
                background: rgba(196,200,199, .5 )
                left: 0
                bottom: 10%
    .container
        max-width: 1200px
    .gallery-first, .gallery-last
        position: relative
        top: 10%
        img
            width: 200px
            
    .gallery-last
        .animation_before
            position: absolute
            bottom: 0
            right: -10%
            img
                width: 100%
    .home_sec1-gallery
        .gallery_center
            position: relative
            z-index: 8
            .gallery_center-img
                position: relative
                display: inline-block
                img
                    width: 400px
                    z-index: 9
                    position: relative
                &::before
                    content: ''
                    width: calc( 100% + 60px )
                    height: calc( 100% + 40px )
                    position: absolute
                    left: -30px
                    bottom: -25%
                    
                    border: 6px solid $blue
            h3
                margin-top: 10px
                font-size: 50px
                font-weight: 400
                margin-bottom: 20px
            .home_sec1-text
                padding-top: 20px
                h5
                    font-size: 40px
                    font-family: "Segoe Script", sans-serif
                p
                    padding: 10px 15px 0
                    text-align: justify
                
            

    @media (max-width: 768px)
        .gallery-first, .gallery-last
            display: none
        .home_sec1-box
            .animation_box-after
                display: none
            .animation_box-before
                &::before
                    display: none
                &::after
                    display: none
    @media (max-width: 576px)
        .home_sec1-gallery
            .gallery_center
                h3
                    font-size: 30px
                .home_sec1-text
                    h5
                        font-size: 28px

// HOME-SECTION-2
.VMN_home-sec2
    padding: 60px 0
    position: relative
    .line_overlay
        position: absolute
        top: 50%
        left: -25px
        transform: translateY(-50%)
        z-index: 1
        img
            width: 100%
    .line_overlay-small
        position: absolute
        bottom: 0
        right: 10%
        // transform: translateY(-50%)
        z-index: 1
        img
            width: 100%
    .home_sec2-box 
        max-width: 992px
        .home_sec2-content
            padding: 50px
            background: #c7cecd
            position: relative
            .line_overlay-content
                position: absolute
                top: -25px
                right: -20px
                img
                    width: 450px
            .line_overlay-content-small
                position: absolute
                bottom: -6%
                left: 6%
            &::before
                content: url('../../assets/img/home/Lines-white.png')
                position: absolute
                top: 40%
                left: 55%
                transform: translate(-50%, -50%)
                width: 200px

            .home_sec2-card
                z-index: 9
                position: relative
                .sec2_card-img
                    position: relative
                    &::before
                        content: ''
                        width: 30%
                        height: 10px
                        position: absolute
                        background-color: #fff
                        left: -15px
                        bottom: -15px
                    &::after
                        content: ''
                        width: 10px
                        height: 70%
                        position: absolute
                        background-color: #fff
                        left: -15px
                        bottom: -15px
                    img
                        position: relative
                        width: 100%
                        z-index: 9
                .sec2_card-title
                    position: relative
                    z-index: 10
                    span
                        font-size: 16px
                        color: #333e48
                        font-weight: 500
                        letter-spacing: 3px
                        padding-top: 3px
                    h3
                        font-size: 45px
                        font-weight: 500
                    p
                        color: #333e48
                        font-size: 14px
                        
            .home_sec2-img
                position: relative
                top: 65%
                transform: translateY(-50%)
                z-index: 9
                &::before
                    content: ''
                    width: 30%
                    height: 10px
                    position: absolute
                    background-color: #fff
                    right: -15px
                    top: -15px
                &::after
                    content: ''
                    width: 10px
                    height: 60%
                    position: absolute
                    background-color: #fff
                    right: -15px
                    top: -15px

                img
                    position: relative
                    width: 100%
                    z-index: 9
        .sec2_title-footer
            p
                color: #414042
    @media (max-width: 768px)
        .home_sec2-box
            .home_sec2-content
                .home_sec2-img 
                    display: none
        .line_overlay-small
            display: none
@media (max-width: 576px)
    .VMN_home-sec2 
        padding-top: 0    
        .line_overlay
            display: none
        .home_sec2-content 
            .line_overlay-content, .line_overlay-content-small
                display: none
// HOME-SECTION-3
.VMN_home-sec3
    padding: 20px 0 170px
    .sec3_items
        padding: 50px 0 0
        .items_detail
            text-align: center
            margin-bottom: 20px
            .items_detail-img
                margin-bottom: 10px
                img
                    height: 100%
            h4
                color: #4d5859
                font-weight: 400
            p
                font-size: 16px
                text-align: justify
                padding: 0 10px
                color: #737e8b
@media (max-width: 768px)
    .VMN_home-sec3
        padding: 20px 0
.VMN_home-sec4
    background: #c7cecd
    height: 300px
    position: relative
    display: flex
    align-items: center
    .sec4_content
        
        .sec4_content-img
            position: absolute
            bottom: 15%
            left: -5%
            width: 50%
            z-index: 9
            .img-boder
                display: inline-block
                position: relative
                img
                    position: relative
                    z-index: 9
                &::before
                    content: ''
                    width: 30%
                    height: 10px
                    position: absolute
                    background-color: #fff
                    left: -15px
                    bottom: -15px
                &::after
                    content: ''
                    width: 10px
                    height: calc( 80% + 10px )
                    position: absolute
                    background-color: #fff
                    left: -15px
                    bottom: -15px
                .content_title-left
                    position: absolute
                    left: -65%
                    top: 50%
                    transform: rotate(-90deg)
                    h3
                        font-size: 50px
                        font-weight: 400
                    p
                        color: #757f8f
                        font-size: 16px
        .sec4_img-line
            position: absolute
            top: 45%
            left: 52%
            transform: translate(-50%, -50%)
            z-index: 1
            img
                // position: relative
                width: 350px
                pointer-events: none
    
    .content_title_right
        position: absolute
        top: -20%
        right: 15%
        z-index: 9
        h3
            font-size: 45px
            opacity: .5
            font-weight: 300
@media (max-width: 1200px)
    .VMN_home-sec4
        .content_title_right
            h3
                font-size: 30px       
@media (max-width: 992px)
    .VMN_home-sec4
        padding: 50px 0
        height: auto
        .content_title_right
            display: none
        .sec4_content
            .sec4_content-img
                position: relative
                bottom: 0
                left: 0
                width: 100%
                text-align: center !important
                z-index: 9 
                .img-boder
                    .content_title-left
                        display: none
           
// Section 5
.VMN_home-sec5
    background: #edeff1
    padding: 50px 0
    position: relative
    .sec5_content-right
        .content_right-title
            margin-bottom: 30px
            h3
                color: #757f8f
                font-size: 38px
                font-weight: 300
            p
                color: #757f8f
                font-size: 16px
        .content_right-carousel
            ul
                padding: 0
                li
                    list-style: none
                    display: flex
                    margin-bottom: 20px
                    .content_img-avt
                        width: 100px
                        margin-right: 10px
                        img
                            margin-top: 10px
                            width: 100%
                            display: block
                    .content-detail
                        p 
                            color: #757f8f
                            font-size: 16px
                            text-align: justify
                            line-height: 1.3
                        h3
                            color: #51534a
                            text-transform: uppercase
                            margin-top: 20px
                            font-size: 15px
                            font-weight: 400
            .slick-dots
                display: flex
                left: 50px
                bottom: -10%
                li
                    margin: 0
                    button
                        &::before
                            font-size: 14px
                            color: #737f8e
    .sec5_content-left
        height: 420px
        .content_left-img
            text-align: center
            position: relative
            top: -55%
            z-index: 9
            .img-box
                img
                    width: 70%
                    // position: relative
                    // z-index: 9 
                .img_border-box
                    position: absolute
                    bottom: -20px
                    left: 50%
                    transform: translateX(-50%)
                    width: calc(100% + 50px)
                    
    .img-overlay
        position: absolute
        top: 70%
        left: 50%
        transform: translate(-50%, -50%)
        pointer-events: none
        img
            width: 300px 
    
@media (max-width: 992px)
    .VMN_home-sec5
        .sec5_content-left
            height: auto
            .content_left-img
                top: 0
@media (max-width: 992px)
    .VMN_home-sec5
        .sec5_content-right
            .content_right-carousel
                .slick-dots
                    bottom: -5%
        .sec5_content-left
            padding-top: 20px
// SECTION 6
.VMN_home-sec6
    
    .sec6_background-img
        position: relative
        img
            width: 100%
        .sec6-title
            position: absolute
            top: 50%
            transform: translateY(-50%)
            h2
                font-size: 50px
                color: #51534a
                font-weight: 300
            p
                font-size: 20px
                color: #51534a
                margin-top: 5px
    @media (max-width: 992px)
        .sec6_background-img
            .sec6-title
                h2
                    font-size: 30px
                p
                    font-size: 16px
    @media (max-width: 576px)
        .sec6_background-img
            .sec6-title
                h2
                    font-size: 22px
                p
                    font-size: 14px
// SECTION 7
.VMN_home-sec7
    padding: 20px 0 80px
    position: relative
    .sec7_gallery-small
        .gallery_small-img
            img
                width: 100%
    .sec7_gallery-big
        height: 100%
        img
            width: 100%
            height: 100%
            object-fit: cover
    .sec7_wallpaper
        position: absolute
        bottom: 0
        right: 0
    @media (max-width: 576px)
        .sec7_gallery-big
            margin-top: 20px
            height: auto
