.img_bg-left
    position: relative
    &::after
        content: ''
        width: calc( 90% + 10px )
        height: 100%
        background: #a0adac
        position: absolute
        top: -10px
        left: -10px
        z-index: 9
    img
        position: relative
        z-index: 10
.img_bg-right
    position: relative
    &::after
        content: ''
        width: calc( 90% + 10px )
        height: 100%
        background: #a0adac
        position: absolute
        top: -10px
        right: -10px
        z-index: 9
    img
        position: relative
        z-index: 10