.VMN_contact-sec1
    padding: 50px 0 30px
    .contact_us
        padding: 40px 5px
        background: #dce1e0
        height: 100%
        .contact_info
            h2
                color: #333e48
                font-weight: 400
                font-size: 30px
                margin-bottom: 20px
            p
                color: #333e48
                font-size: 16px
                padding: 0 20px 
                a
                    word-break: break-word
                &:hover
                    a
                        color: #333e48
            .icons_social
                i
                    width: 40px
                    height: 40px
                    line-height: 40px
                    background: #394049
                    font-size: 18px
                    border-radius: 50%
                    color: #fff
                    margin: 0 5px
                
.VMN_contact-sec2
    padding: 50px 0
    .contact_map
        iframe
            width: 100%
            height: 500px
.VMN_contact-sec3
    padding: 20px 0 50px
    .sec3_title-form
        h2
            color: $blue
            font-weight: 300
            font-size: 60px
            letter-spacing: 5px
            margin-bottom: 15px
    .contact_us-form
        max-width: 600px
        margin: 0 auto
        .contact_input

            input
                width: 100%
                background: #f3f3f3
                border: 1px solid #747f8d !important
                padding: 5px
                margin-bottom: 20px 
                &::placeholder
                    color: #b7b8ba
                    font-family: 'Segoe Script'
                    padding-left: 5px
        .contact_area
            textarea
                background: #f3f3f3
                height: 170px
                border: 1px solid #747f8d!important
                &::placeholder
                    color: #b7b8ba
                    font-family: 'Segoe Script'
                    
        .button_main
            background: #ccd3d2
            color: #fff
            margin-top: 20px
            border-radius: 30px
            box-shadow: none
            display: inline-block
            text-transform: uppercase
            transition: .5s all
            padding: 5px 30px
            &:hover
                background: #747f8d
                color: #fff
@media (max-width: 768px)
    .VMN_contact-sec2
        .contact_map
            iframe
                height: 300px
    .VMN_contact-sec3
        .sec3_title-form
            h2
                font-size: 30px
        .contact_us-form
            margin-top: 20px
        